:root {
  --primary-color: #2c5282;
  --secondary-color: #4299e1;
  --accent-color: #ebf8ff;
  --text-color: #2d3748;
  --border-color: #cbd5e0;
  --success-color: #48bb78;
  --error-color: #f56565;
}

body {
  color: var(--text-color);
  background-color: #f8fafc;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
  line-height: 1.6;
}

header {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
  display: flex;
  position: relative;
}

header img {
  float: left;
  width: 50px;
  height: 50px;
  margin-top: 16px;
}

h1, h2, h3 {
  color: var(--primary-color);
  margin-bottom: .5em;
}

.container {
  background-color: #fff;
  border-radius: 8px;
  margin-bottom: 30px;
  padding: 25px;
  box-shadow: 0 4px 6px #0000001a;
}

.file-upload {
  border: 2px dashed var(--border-color);
  background-color: var(--accent-color);
  border-radius: 8px;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
  padding: 30px;
  transition: all .3s;
  display: flex;
}

.file-upload:hover {
  border-color: var(--secondary-color);
}

.file-upload-input {
  display: none;
}

.file-upload-label {
  background-color: var(--primary-color);
  color: #fff;
  cursor: pointer;
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  padding: 10px 20px;
  font-weight: 600;
  transition: background-color .3s;
  display: flex;
}

.file-upload-label:hover {
  background-color: var(--secondary-color);
}

.preview-area {
  border: 1px solid var(--border-color);
  border-radius: 4px;
  max-height: 300px;
  margin-top: 20px;
  padding: 15px;
  overflow-y: auto;
}

table {
  border-collapse: collapse;
  width: 100%;
  margin-bottom: 20px;
}

th, td {
  text-align: left;
  border-bottom: 1px solid var(--border-color);
  padding: 12px 15px;
}

th {
  background-color: var(--accent-color);
  color: var(--primary-color);
  font-weight: 600;
}

tr:hover {
  background-color: #ecf6fc80;
}

.search-container {
  float: right;
  margin-top: .7em;
  margin-bottom: .5em;
  position: relative;
}

.search-container-mapping {
  width: 170px;
  height: 33.5px;
  margin: 0;
  padding: 0;
  position: relative;
}

.search-input {
  border: 1px solid var(--border-color);
  border-radius: 4px;
  padding: 10px 15px;
  font-size: 16px;
  transition: border-color .3s;
}

.search-input:focus {
  border-color: var(--secondary-color);
  outline: none;
}

.search-input-mapping {
  border: 1px solid var(--border-color);
  border-radius: 4px;
  width: 100%;
  height: 11.5px;
  padding: 10px 15px;
  font-size: 16px;
  transition: border-color .3s;
}

.search-input-mapping:focus {
  border-color: var(--secondary-color);
  outline: none;
}

.searchable-dropdown {
  width: 100%;
  position: relative;
}

.dropdown-content {
  border: 1px solid var(--border-color);
  z-index: 100;
  background-color: #fff;
  border-radius: 4px;
  width: 100%;
  max-height: 200px;
  display: none;
  position: absolute;
  overflow-y: auto;
  box-shadow: 0 4px 6px #0000001a;
}

.dropdown-item {
  cursor: pointer;
  padding: 10px 15px;
  transition: background-color .2s;
}

.dropdown-item:hover {
  background-color: var(--accent-color);
}

.button {
  background-color: var(--primary-color);
  color: #fff;
  cursor: pointer;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  font-weight: 600;
  transition: background-color .3s;
}

.button:hover {
  background-color: var(--secondary-color);
}

.button:disabled {
  background-color: var(--border-color);
  cursor: not-allowed;
}

.info-icon {
  color: var(--secondary-color);
  cursor: pointer;
  font-size: 24px;
  position: absolute;
  top: 0;
  right: 0;
}

.tooltip {
  visibility: hidden;
  border: 1px solid var(--border-color);
  z-index: 200;
  opacity: 0;
  background-color: #fff;
  border-radius: 4px;
  width: 450px;
  padding: 2px 4px 2px 15px;
  font-size: 15px;
  transition: opacity .3s;
  position: absolute;
  top: 30px;
  right: 0;
  box-shadow: 0 4px 6px #0000001a;
}

.info-icon:hover .tooltip {
  visibility: visible;
  opacity: 1;
}

.mapping-controls {
  align-items: center;
  gap: 10px;
  margin-bottom: 10px;
  display: flex;
}

.field-mapping-row {
  align-items: center;
  gap: 15px;
  margin-bottom: 15px;
  display: flex;
}

.preview-value {
  background-color: var(--accent-color);
  text-overflow: ellipsis;
  white-space: nowrap;
  border-radius: 4px;
  max-width: 200px;
  padding: 5px 10px;
  overflow: hidden;
}

.delimiter-input, .position-input {
  border: 1px solid var(--border-color);
  border-radius: 4px;
  width: 80px;
  padding: 8px;
}

.delimiter-input:focus, .position-input:focus {
  border-color: var(--secondary-color);
  outline: none;
}

.success-message {
  color: #155724;
  background-color: #d4edda;
  border-radius: 4px;
  margin-bottom: 20px;
  padding: 10px 15px;
  display: none;
}

.error-message {
  color: #721c24;
  background-color: #f8d7da;
  border-radius: 4px;
  margin-bottom: 20px;
  padding: 10px 15px;
  display: none;
}

.popup-container {
  z-index: 1000;
  background-color: #00000080;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.popup {
  background-color: #fff;
  border-radius: 5px;
  padding: 20px;
  box-shadow: 0 2px 10px #0003;
}

.popup input[type="text"] {
  border-color: var(--secondary-color);
  outline: none;
  width: 250px;
  margin-bottom: 10px;
  padding: 8px;
}
